footer{
    background: var(--color-cards);
    padding:3rem 0;
    text-align: center;
    font-size: 0.9rem;
}
footer a{
    color: var(--color-bg);
}
.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
    color: var(--color-text);
}
.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
    cursor: pointer;

}
.footer__socials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}
.footer__socials a{
    background:black;
    color: var(--color-white);
    padding:0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}
.footer__socials a [data-theme="dark"]{
background:var(--color-text);
color: var(--color-white);
}
.footer__social a:hover{
    background:transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}
.footer__copyright{
    margin-bottom: 4rem;
    color: var(--color-text);

}
.permalinks li > a{
    color: var(--color-text);
}
@media screen and (max-width:600px) {
    .permalinks{
        width: 100%;
        flex-direction: column;
        justify-content:space-evenly;
        padding-right:2.3rem;

    }
    .footer__socials{
        margin-bottom: 2.6rem;
    }
    li{
        list-style: none;
    }
}
