body {
  margin: 0;
  width:-webkit-fit-content;
  width:-moz-fit-content;
  width:fit-content;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root{
  --color:#645caa;
  --color-bg-variant:#a084ca;
  --color-primary:#bfaced;
  --color-primary-variant:#ebc7e8;
  --color-white:#fff;
  --color-light:rgba(255,255,255,0.6);
  --container-width-lg:75%;
  --container-width-md:86%;
  -container-width-sm:100%;
  --transition: all 400ms ease out;
  --image-size:300px;
  --blob-color:linear-gradient(rgb(219, 212, 228),rgb(139, 82, 185));
  --color-cards:var(--color-bg-variant);
  --color-hover:linear-gradient(145deg, #a0264e, #bc22cd);
  --color-text:white;
  --color-text2:black;
  --box-shadow-hover:12px 12px 24px #0f0f1b,
  -12px -12px 24px #2f2f55;
  --color-nav-active:linear-gradient(145deg,var(--color-white),var(--color-bg-variant));
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}



.headder_container{
    background-color: var(--color-bg-variant);
    border: 0.1px solid black;
    position:-webkit-sticky;
    position:sticky;
}
.nav_container{
    
    background-color: var(--color-bg);
}
.navbar{
    margin-top: 1rem;
    position:-webkit-sticky;
    position:sticky;
    display: flex;
    width: 100%;
    flex-direction: column;
    /* padding-bottom:50rem ; */
    border-radius: 1rem;
}
.tab_container_2{
    display: grid;
    grid-template-columns: auto;

   
}
.tab_container{
    display: flex;
    flex-direction: column;
    background-color: var(--color-bg-variant);
}
Tab{
    border: 2px solid black;
    list-style:symbols('#');
}
.tabscon{
    margin: 2rem;
    padding: 2rem;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    border-radius: 42px;
    border-radius: 42px;
    background: #7c62a3;
    box-shadow:  5px 5px 10px #4c3c63,
                 -5px -5px 10px #ac88e3;
}

footer{
    background: var(--color-cards);
    padding:3rem 0;
    text-align: center;
    font-size: 0.9rem;
}
footer a{
    color: var(--color-bg);
}
.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
    color: var(--color-text);
}
.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
    cursor: pointer;

}
.footer__socials{
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}
.footer__socials a{
    background:black;
    color: var(--color-white);
    padding:0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}
.footer__socials a [data-theme="dark"]{
background:var(--color-text);
color: var(--color-white);
}
.footer__social a:hover{
    background:transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}
.footer__copyright{
    margin-bottom: 4rem;
    color: var(--color-text);

}
.permalinks li > a{
    color: var(--color-text);
}
@media screen and (max-width:600px) {
    .permalinks{
        width: 100%;
        flex-direction: column;
        justify-content:space-evenly;
        padding-right:2.3rem;

    }
    .footer__socials{
        margin-bottom: 2.6rem;
    }
    li{
        list-style: none;
    }
}
/* upgraded on june 3 */
.body_container{
    display: grid;
    grid-template-columns: auto;
    background-color:#cdf0ea;
    padding: 4rem;
}


